<template>
    <div id= "setUp">
        <div class= "setUpHead">{{title}}</div>
        <div class= "setUpContent">
            <ul class= "setUpTabel">
                <li v-for= "(item,i) in setUpArr"  @click= "switchs(i)" :ref= "i+1" :key= "i" :class= "showId == i?'active':''">{{item.name}}</li>
                <b ref= "activeBorder"></b>
            </ul>
            <div class= "selecterBox">
                <div v-for= "(item,i) in setUpArr" :key= "i" v-show= "showId == i">
                    <ul>
                        <li v-for= "(index,j) in item.selecter" :key= "j">
                            <span>{{index.name}}</span>
                            <em :style= "{fontFamily :index.fontfamily ,backgroundColor:index.background }">{{index.sample}}</em>
                            <i class="iconfont icon-xuanzhong" @click= "SelectFontFamily(index.fontfamily,j)" :class= "fontFamily == j?'SelectedActive':''" v-if= "item.name == '文字设置'"></i>
                            <i class="iconfont icon-xuanzhong" @click= "SelectBackgroundColor(index.background,j)" :class= "backgroundColor == j?'SelectedActive':''" v-if= "item.name == '背景色设置'"></i>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'setUp',
    data () {
        return {
            title: '设置',
            setUpArr: [
                {
                    name: '文字设置',
                    selecter: [
                        {
                            name: '默认',
                            sample: '这是一个帅哥',
                            fontfamily:'仿宋体'
                        },{
                            name: '宋体',
                            sample: '这是一个帅哥',
                            fontfamily:'宋体'
                        },{
                            name: '楷体',
                            sample: '这是一个帅哥',
                            fontfamily:'楷体'
                        }
                    ]

                },{
                    name: '背景色设置',
                    selecter: [
                        {
                            name: '默认',
                            sample: '这是一个帅哥',
                            background:'#fff'
                        },{
                            name: '褐色',
                            sample: '这是一个帅哥',
                            background:'#928e8e'
                        }
                    ]
                }
            ],
            showId: 0,
            fontFamily: 0,
            backgroundColor: 0
        }
    },
    methods: {
        //tabel切换高亮
        switchs (id){
            //显示高亮
            this.showId = id
            this.$refs.activeBorder.style.width = $(this.$refs[id]).width() + 'px';
            this.$refs.activeBorder.style.left = $(this.$refs[id]).width()*id + 'px';
            if(id == 0){
                this.$refs.activeBorder.style.left = 0;
            }
        },
        //字体选中高亮
        SelectFontFamily (font,id) {
            this.fontFamily = id
            this.$store.dispatch('setFontFamily',{font,id})

        },
        //背景色选中高亮
        SelectBackgroundColor (background,id) {
            this.backgroundColor = id
            this.$store.dispatch('setBackground',{background,id})
        }
    },
    mounted () {
        if(this.showId == 0){
            this.$refs.activeBorder.style.width = $(this.$refs[1]).width() + 'px';
            this.$refs.activeBorder.style.left = 0;
        }

        $('#setUp').css({
            "background-color": this.$store.state.setUpPage.background
        })
  
    },
    created () {
        this.fontFamily = this.$store.state.setUpPage.fontFamilyId;
        this.backgroundColor = this.$store.state.setUpPage.backgroundId;
    },
    computed: {
        getBack() {
            return this.$store.state.setUpPage.background;
        }
    },
    watch: {
        getBack(val) {
            $('#setUp').css({
                "background-color": val
            })
            
        }
    }

    
}
</script>

<style lang="scss" scoped>
    #setUp{
        .setUpHead{
            text-align: center;
            line-height: 60px;
            font-size:20px;
            font-weight:600;
        }
        .setUpContent{
            .setUpTabel{
                background:#ddd;
                position: relative;
                width:100%;
                height:50px;
                display: flex;
                justify-content: space-around;
                line-height: 50px;
                li{
                    position: relative;
                    cursor: pointer;
                    flex:1;
                    text-align: center;
                    color:#8c8b8b;
                    font-weight:600;
                }
                b{
                    transition: left 0.5s;
                    position: absolute;
                    bottom:0;
                    left:0;
                    height:4px;
                    background:$background;
                }
                .active{
                    color:$background;
                }
                
            }
            .selecterBox{
                ul{
                    padding:10px 40px;
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    li{ 
                        border:1px solid #ddd;
                        border-radius: 4px;
                        position: relative; 
                        margin-top:30px;                 
                        width:200px;               
                        span{
                            padding-left:10px;
                            display: block;
                            line-height:40px;
                            background:#ddd;
                            border-radius: 4px 4px 0 0;
                            color:#FFF;
                        }
                        em{
                            text-align: center;
                            line-height: 100px;
                            height:100px;
                            display: block;
                            border-radius: 0 0 4px 4px;
                        }
                        i{
                            cursor: pointer;
                            position: absolute;
                            top:2px;
                            right:2px;
                            font-size:18px;
                            color:#fff;
                        }
                        .SelectedActive{
                            color:$background;
                        }
                    }
                }
            }
        }
    }
</style>







