<template>
    <div id= "content">
            <div class="contentHead">
                
                    <a href= "javasctipt:0;">
                        <img :src= "home.icon"/>
                    </a>
                    <h3>{{ home.name }}</h3>
                    <p>{{ home.brief }}</p>
                
            </div>
            <router-view class="contentbox"></router-view>
       
    </div>
</template>

<script>

export default {
    name: 'Content',
     data () {
        return {
            home: {
                name: 'vonne',
                icon: require('../../../static/img/icon.jpg'),
                brief: '啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊'
            },
            data: [
                {
                    icon: 'iconfont icon-shouji',
                    phone: 17688881701
                },{
                    icon: 'iconfont icon-youxiang',
                    phone: '17688881701@163.com'
                },{
                    icon: 'iconfont icon-aui-icon-qq',
                    phone: 1601047350
                }
            ]
            

        }
       
    },
    mounted (){
        var self= this;
        $('#content').scroll(function () {
            //滚动条高度
            let scrollTop = $(this).scrollTop();
            //内容区域高度
            let scrollHeight = $('.contentbox').height();
            //窗口高度
            let clientHeight = $(this).height();
        
            if (scrollHeight - scrollTop <= clientHeight ) {
                self.$children[0].page++;
                if (self.$children[0].page >= self.$children[0].totalPage){
                    self.$children[0].page = self.$children[0].totalPage
                }
                                 
            }
            
        })     
    }
   
}
</script>

<style lang="scss" scoped>
    #content{
        width:67%;
        height:100%;
        overflow: auto;
        border-radius: 6px;
        .contentHead{
            display: none;
        }
       
       
    }
    ::-webkit-scrollbar { width: 0;}
    ::-o-scrollbar { 
        -moz-appearance: none !important;   
        background: rgba(0,255,0,0) !important;
    }

    .contentbox{
        min-height:600px;
        animation: myfirst 0.5s;
        -webkit-overflow-scrolling: touch;
    }
    @-webkit-keyframes myfirst 
    {
        from {
           transform: scale(0);
        }
        to {
            transform: scale(1);
        }
    }

    
    @media screen and (max-width: 1180px) {
      #content{
         
          width:100%;
          height:610px;
         
          margin-bottom:vw(10);
            .contentHead{
                display: none;
            }
      }
  
    }
    @media screen  and (max-width: 560px){
      #content{
         border-radius:0;
         margin-bottom:vw(100);
         height:vw(1600);
         .contentHead{
           display: block;
            background:$background;
            padding:vw(20) vw(50);
           
            
            text-align: center;
            a{
                display: block;
                width:vw(120);
                height:vw(120);
                border-radius: 50%;
                overflow: hidden;
                border:vw(8) solid #3b6eb4;
                margin:0 auto;
                img{
                    
                    border:0;
                    width:100%;
                }
            }
            h3{
                color:$color;
                font-size:vw(35);
            }
            p{
                margin:0 auto;   
                width:85%;
                color:$color;
                font-size:vw(24);
            }
            
            
        }
         
      }
    }
</style>





