import * as types from '../mutation-types';

const state = {
    fontfamliy : '仿宋体',
    fontFamilyId: 0,
    background : '#fff',
    backgroundId:0,
}

const actions = {
    setFontFamily ({ commit },font){
    
        commit(types.SET_FONTFAMILY,font)
    }, 
    setBackground ({ commit}, background){
        commit(types.SET_BACKGROUND,background)
    }
}

const mutations = {
    [types.SET_FONTFAMILY](state, bool) {
        
        state.fontfamliy = bool.font,
        state.fontFamilyId = bool.id
    },
    [types.SET_BACKGROUND](state, bool) {
        state.background = bool.background,
        state.backgroundId = bool.id
    }
}


export default {
    state,
    actions,
    mutations
}


