<template>
    <div id= "header">
       <div class= "headTop">
           <a href= "javasctipt:0;">
               <img :src= "home.icon"/>
           </a>
           <h3>{{ home.name }}</h3>
           <p>{{ home.brief }}</p>
       </div>
       <div class= "headBottom">
           <ol>
               <li v-for= "(item,i) in data" :key= "i">
                   <i :class= "item.icon"></i>
                   <span>{{ item.phone }}</span>
               </li>
           </ol>
       </div>
    </div>
</template>

<script>
export default {
    name: 'Head',
    data () {
        return {
            home: {
                name: 'vonne',
                icon: require('../../../static/img/icon.jpg'),
                brief: '啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊啊'
            },
            data: [
                {
                    icon: 'iconfont icon-shouji',
                    phone: 17688881701
                },{
                    icon: 'iconfont icon-youxiang',
                    phone: '17688881701@163.com'
                },{
                    icon: 'iconfont icon-aui-icon-qq',
                    phone: 1601047350
                }
            ]
            

        }
       
    }
}
</script>

<style lang="scss" scoped>
    #header{
        padding:25px 30px;
        width:240px;
        height:91%;
        background:$background;
        border-radius: 6px;
        color:$color;
        font-size:12px;
        .headTop{
            overflow: hidden;
            width: 100%;
            text-align: center;
            border-bottom:1px solid $color;
            padding-bottom:25px;
            a{
                margin:0 auto;
                display: block;
                width:100px;
                height:100px;
                border-radius: 50%;
                overflow: hidden;
                border:8px solid #3b6eb4;
                margin-bottom:0;
                img{
                    border:0;
                    width:100%;
                }
            }
            h3{
                line-height: 60px;
                font-size:24px;
                
            }
            p{
                text-align: left;
                font-size:14px;
            }
        }
        .headBottom{
            border-bottom: 1px solid $color;
            ol{
                padding:25px 0;
                li{
                    line-height: 24px;
                    i{
                        float:left;
                        margin-right:10px;
                    }
                    
                }
            }
        }
    }

    @media screen and (max-width: 1180px) {
      #header{
          
        width:auto;
        overflow: hidden;
        display: flex;
        margin-bottom:10px;
            .headTop{
                width:49%;
                border-bottom:0;
                border-right:1px solid $color;
                padding-right:5px;

                padding-bottom:0;
                a{
                    float: left;
            
                }
                h3{
                    line-height: 40px;
                }
                p{
                    width:62%;
                    float:right;
                    margin-right:2%;
                   
                }
           }
           .headBottom{
               border-bottom:0;
               width:49%;
               ol{
                   padding:0 0 0 20px;
                   li{
                       margin:10px 0;
                   }
               }
           }
      }
  
    }

    @media screen  and (max-width: 785px){
      #header{
          .headTop{
              
              
                a{
                  width:vw(110);
                  height:auto;
                  border:vw(8) solid #3b6eb4;
                }
                h3{
                    font-size:vw(16);
                    line-height:vw(30);
                }
                p{
                    width:vw(160);
                    font-size:vw(12);
                }
                
          }
      }
  
    }
    @media screen  and (max-width: 560px){
      #header{

        border-radius:0;
        margin-bottom:0;
        display: none;
        padding:vw(20) vw(50);
         .headTop{
             width:100%;
             border:0;
            a{
                border-radius:50%;
            }
            a,p{
                margin:0 auto;
                float: none;
            
            }
            h3{
                 font-size:vw(30);
            }
            p{
                width:85%;
                text-align: center;    
                font-size:vw(12);
            }
            
            
         }
         .headBottom{
             display: none;
        }
      }
  
    }
</style>



