<template>
  <div id="wangeditorBox">
    <div id="wangeditorHead" class="toolbar"></div>
    <div style="padding: 5px 0; color: #ccc"></div>
    <div id="wangeditorHTML" class="text"></div>
  </div>
</template>

<script>
  import E from 'wangeditor'
  export default {
      name: 'editor',
      data () {
        return {
          editorContent: ''
        }
      },
      props:['ueditor'],
      mounted() {
        var self = this;
        var editor = new E('#wangeditorHead','#wangeditorHTML')

        //实时更新当前内容
        editor.customConfig.onchange = (html) => {
          this.editorContent = html
        }
        //隐藏网络上传图片tab
        editor.customConfig.showLinkImg = false

        // //自定义图片上传
        editor.customConfig.customUploadImg = function (files, insert) {
            // files 是 input 中选中的文件列表
            // insert 是获取图片 url 后，插入到编辑器的方法

            // 上传代码返回结果之后，将图片插入到编辑器中
            var formData = new FormData();
            $(files).each(function(index,item){
                
                formData.append('files',item);
            })

           $.ajax({
				url: 'http://45.76.169.191:80/upload',
				type: 'POST',
				cache: false, //不必须
				data: formData,
				processData: false,
				contentType: false,
				success: function(data) {
					$(data.files).each(function(index,item){
                        insert("../../../static/img/"+item.filename)
                    })
				}
            })
        }

        //限制图片上传数量
        editor.customConfig.uploadImgMaxLength = 9

        //配置表情
        editor.customConfig.emotions = [
            {
                // tab 的标题
                title: '默认',
                // type -> 'emoji' / 'image'
                type: 'image',
                // content -> 数组
                content: [
                    {
                        alt: '[坏笑]',
                        src: 'http://pic1.sc.chinaz.com/Files/pic/Listfaces/4983/01.gif'
                    },
                    {
                        alt: '[舔屏]',
                        src: 'http://pic1.sc.chinaz.com/Files/pic/Listfaces/4983/01.gif'
                    }
                ]
            }
        ]
       

        //当编辑器获得焦点时触发
        //editor.customConfig.onfocus = function (html) {
        //   editor.txt.clear()
        // }
        //当编辑器失去焦点时触发
        editor.customConfig.onblur = function (html) {

            self.$emit('transmit',self.editorContent)
        }
        editor.create()

        //请输入内容
        editor.txt.html(self.ueditor)
    
      }
    }
</script>

<style lang="scss" scoped>
 #wangeditorBox{
    .toolbar{
        background:#ddd;
        flex-wrap: wrap; 
        padding:5px 0;
        border-radius: 4px;
    }
    #wangeditorHTML{
        background:#fff;
        min-height:300px;
        padding:5px;
        border-radius: 4px;
        
    }
    
 }
   
</style>





