<template>
    <div id= "looding">
        <h2>{{msg}}</h2>
        <form>
            <label for= "name">账号 :&nbsp;&nbsp;&nbsp; </label>
            <input type= "text" name= "name" id ="name" @click= "getFocus ()" ref= "name" placeholder="例如：手机号"/>
            <br/>
            <label for= "password">密码 :&nbsp;&nbsp;&nbsp; </label>
            <input type= "password" name= "password" ref= "password" id ="password" @click= "getFocus ()"/>
            <br/> 
            <label class= "loodingFont">登录 :&nbsp;&nbsp;&nbsp; </label>
            <button  @click.prevent = "looding()">登&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;录</button>        
        </form>
        
    </div>
</template>

<script>
export default {
    name : 'SignIn',
    data () {
        return {
            msg : '欢迎登陆后台管理系统',
            url : 'http://45.76.169.191:80'
        }
    },
    methods : {
        getFocus () {
            $('input').each(function(i,item){
                if($(item).is(':focus')){
                    $(item).addClass('active').removeClass("activeEror").attr({'placeholder':''}).siblings().removeClass("active");
                }
            })
        },
        looding(){
            var self = this;
            //获取账号，密码
            var name = this.$refs.name.value;
            var password = this.$refs.password.value;;
            $.ajax({
                type:'post',
                url:`${self.url}/getuser`,
                data:{
                    name:name,
                    password:password
                },
                success:function(data){
                    console.log(data)
                    if(data){
                        alert(data);
                    }else{
                        self.$router.push({ path: 'backstage' })
                    }
                }
            })
        }
    },
    mounted () {
        
        $('#name').focus();
        this.getFocus ();
        $('#name').blur(function(){
            var self = this;
            let name = $(this).val().trim()
            if(name == ''){
                $(this).addClass('activeEror').attr({'placeholder':'账号不能为空！'}).siblings().removeClass("activeEror");
            }else{
                $.ajax({
                    type:'post',
                    url:'http://45.76.169.191:80/getuser',
                    data:{
                        name:name
                    },
                    success:function(data){
                        if(data){
                            $(self).val('').attr({'placeholder':data}).addClass('activeEror').siblings().removeClass("activeEror");
                        }
                    }
                })
            }
        });
    }
}
</script>

<style lang="scss" scoped>
    #looding{
        
        h2{
            text-align: center;
            color:#fff;
            line-height:150px;
        }
        form{
            margin-top:40px;
            text-align: center;
            label{
                color:#fff;
                
            }
            input{
                outline: none;
                margin-bottom:30px;
                border:0;
                padding:5px 10px;
                border-radius: 4px;
                width:220px;
                height:30px;
            }
            .active{
                background:$background;
                color:#fff;
            }
            .activeEror{
                background:red;
            }
            .loodingFont{
                color:rgba(0,0,0,.1);
            }
            button{      
                
                margin:20px auto;
                width:240px;
                border-radius: 4px;
                height:40px;
                line-height: 40px;
                color:#fff;
                background:$background;
            }
            
        }
       
    }
</style>





