import Vue from 'vue'
import Router from 'vue-router'

import HelloWorld from '@/components/HelloWorld'
import Looding from '@/components/Looding'

import List from '@/components/list/listPage'
import setUp from '@/components/setUp/setUpPage'
import Backstage from '@/components/backstage/Backstage'
import Edit from '@/components/editPage/editPage'
import SignIn from '@/components/signIn/signIn'
import Details from '@/components/details/detailsPage'




Vue.use(Router)

export default new Router({
  
  routes: [
    {
      path: '/',
      redirect: '/list',
      name: 'HelloWorld',
      component: HelloWorld,
      children: [
        {
          path: 'list',
          name: 'List',
          component: List
        },{
          path: 'setUp',
          name: 'setUp',
          component: setUp
        }, {
          path: 'details',
          name: 'Details',
          component: Details
        }
      ]
    },{
      path: '/looding',
      redirect: '/looding/signIn',
      name: 'Looding',
      component: Looding,
      children: [
        {
          path: 'signIn',
          name: 'SignIn',
          component: SignIn
        },{
          path: 'backstage',
          name: 'Backstage',
          component: Backstage
        },{
          path: 'edit',
          name: 'Edit',
          component: Edit
        }
      ]
    } 
  ]
})



