<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed: {
    getFont() {
      return this.$store.state.setUpPage.fontfamliy;
    }
  },
  watch: {
    getFont(val) {
      $('body').css({
        "font-family": val
      })
    }
  }
}
</script>

<style>
  body{
    margin:0;
    padding:0;
    background:url(../static/img/background.png);
    box-sizing:border-box;
    font-family: "仿宋体,黑体,黑体简";
    font-size: 16px;
    -webkit-overflow-scrolling: touch;
    height:auto;
  }
  html{
    height:auto;
  }

  ul,h3,p,ol,h2,h1,h4,h5,h6{
    margin:0;
    padding:0;
  }

  em{
    font-style:normal;
  }

  a{
    text-decoration: none;
  }

  li{
    list-style:none;
  }
</style>



