<template>
    <div id= "details">
        <h3>{{data.title}}</h3>
        <div class= "detailsClicks">
            <div class= "detailsBrowsebox">
                <i class="iconfont icon-yanjing"></i>
                <em>{{data.browse}}</em>
            </div>
            <div class= "detailsComments">
                <span>
                    <i class="iconfont icon-z-likeFill" @click= "Fabulous" ref="Fabulous"></i>
                    <em >{{data.Fabulous}}</em>
                </span>
                <span>
                    <i class="iconfont icon-z-nolikeFill" @click= "water" ref="water"></i>
                    <em >{{data.water}}</em>
                </span>
            </div>
        </div>
        <div class= "detailsContent" v-html="data.ueditor"></div>
    </div>
</template>

<script>
export default {
    name : "Details",
    data () {
        return {
            data:{},
            unFabulous:true,
            unwater:true,
            url:'http://45.76.169.191:80'
        }
    },
    mounted (){
        var id = this.$route.query.id;
        var self = this;
        $.ajax({
            type:'post',
            url:`${self.url}/getList`,
            data:{
                id:id
            },
            success:function(data){

                self.data = JSON.parse(data).data[0];
                self.data.browse++;
            }
        })

        $('#details').css({
            "background-color": this.$store.state.setUpPage.background
        })
    },
    methods:{
        Fabulous(){

            if(this.unFabulous){
                this.data.Fabulous++;
                $(this.$refs.Fabulous).css('color','red');
                //取消点击
                this.unFabulous = false;
            }
          
        },
        water(){
            
            if(this.unwater){
                this.data.water++;
                $(this.$refs.water).css('color','red');
                //取消点击
                this.unwater = false;
            }
        }
    },
    destroyed () {
        var self = this;
         $.ajax({
            type:'post',
            url:`${self.url}/updateDate`,
            data:self.data,
            success:function(data){
               
            }
        })
    },
    computed: {
        getBack() {
            return this.$store.state.setUpPage.background;
        }
    },
    watch: {
        getBack(val) {
            $('#details').css({
                "background-color": val
            })
            
        }
    }

}
</script>

<style lang="scss" scoped>
    #details{
        padding:20px;
        h3{
            text-align: center;
            line-height:80px;
        }
        .detailsClicks{
            display: flex;
            justify-content: space-between;
            padding-bottom:10px;
            border-bottom:1px solid #afadad;
            i,em{
                color:#545252;
                cursor: pointer;
            }
            
        }
        .detailsContent{
            padding:10px 5px;
        }
    }
    @media screen  and (max-width: 560px){
      #details{

          h3{
                
                line-height:vw(40);
            }
          
      }
  
    }
</style>





