<template>
    <div id= "backstage">
        <table border="0" cellspacing="0" cellpadding="0">
            <thead>
                <tr>
                    <td><input type="checkbox" class="all"/></td>
                    <td>title</td>
                    <td>brief</td>
                    <td>img</td>
                    <td>browse</td>
                    <td>Fabulous</td>
                    <td>water</td>
                    <td>更多操作</td>
                </tr>
            </thead>
            <tbody v-if= "arr">
                <tr v-for= "(item,i) in arr" :key= "i" :data-id= "item.id" ref="dataEle">
                    <td><input type="checkbox"/></td>
                    <td>{{item.title}}</td>
                    <td :title= "item.brief">{{item.brief}}</td>
                    <td>{{item.img}}</td>
                    <td>{{item.browse}}</td>
                    <td>{{item.Fabulous}}</td>
                    <td>{{item.water}}</td>
                    <td>
                        <span class="backEdit" @click= "editData(item.id)">编辑</span>
                        <span class="backDelete" @click= "deleteData(item.id)">删除</span>
                    </td>
                </tr>
            </tbody>
        </table>
        <div class="backPage">
            <em class= "addTo" @click= "editData()">添加</em>
            
            <span v-for= "(a,i) in buttonNum" :key= "i" :class= "i+1 == page?'active':''" @click= "setPage(i+1)">{{i+1}}</span>
        </div>
        
    </div>
</template>

<script>
export default {
    name : 'Backstage',
    data (){
        return {
            arr:null,
            page:1,
            num:6,
            buttonNum:0,
            url:'http://45.76.169.191:80'
        }
    },
    methods: {
        //获取数据
        ajaxData () {
            var self = this;
            $.ajax({
                type:'post',
                url:self.url + "/getList",
                data:{
                    page:self.page,
                    num:self.num
                },
                success:function (data) {
                    self.arr = JSON.parse(data).data;
                    self.buttonNum = Math.ceil(JSON.parse(data).total/self.num);
                }
            })
        },
        //修改翻页
        setPage (pageId) {
           this.page = pageId
        },
        //删除
        deleteData (id) {
            var self = this;
        
            //删除节点
            const eles = this.$refs.dataEle;
            $(eles).each(function(index,item){
                if($(item).attr('data-id') == id){
                    $(item).remove();
                }
            })
            
            //删除数据
            $.ajax({
                type:'post',
                url:self.url + "/deleteDate",
                data:{
                    id:id
                }
            })
        },
        //编辑
        editData (id) {
            if(id){
                this.$router.push({ path: 'edit',query: {id: id }})
            }else{
                this.$router.push({ path: 'edit'})
            }
            

        }
    },
    mounted () {
        this.ajaxData()
    },
    watch :{
        page : function() {
            this.ajaxData()
        }
    }
}
</script>

<style lang="scss" scoped>
    #backstage{
        table{
            width:85%;
            margin:0 auto;
            border-collapse: collapse;
            background:#fff;
            thead{
                color:#fff;
                background:$background;
                tr{
                    td{
                        text-align: center;
                        border:1px solid #3b6eb4;
                        padding:5px;
                        min-width:40px;
                        height:40px;
                    }
                }
            }
            tbody{
                tr{
                    td{
                        text-align: center;
                        height:50px;
                        font-size:14px;
                        border:1px solid #ddd;
                        &:nth-child(3){
                            text-align: left;
                            max-width:240px;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            padding:2px 8px;
                        }
                        span{
                            display: inline-block;
                            border:1px solid #ddd;
                            padding:4px 6px;
                            border-radius: 4px;
                            cursor: pointer;
                            background:$background;
                            color:#fff;
                            font-size:12px;
                        }
                        .backDelete{
                            background:red;
                        }
                    }
                }
            }
        }
        .backPage{
            width:85%;
            margin:0 auto;
            margin-top:20px;
            text-align: right;
            .addTo{
                display: inline-block;
                width:70px;
                height:35px;
                line-height:35px;
                text-align: center;
                color:#fff;
                background:$background;
                float:left;
                border-radius: 4px;
                cursor: pointer;
            }
            span{
               display: block;
                display: inline-block;
                width:30px;
                height:30px;
                background:#fff;
                text-align: center;
                line-height:30px;
                border:1px solid #ddd;
                margin-left:10px;
                border-radius: 4px;
                cursor: pointer;
            }
            .active{
                background:$background;
                color:#a59999;
                border:1px solid $background;
            }
        }
    }
</style>






