<template>
  <div id= "footer">
      <ul>
          <li v-for= "(item,i) in list" :key= "i" @click= "goList(item.href,item.id)" :class= "item.id==showId?'active':''">
              <i :class= "item.icon" :title= "item.title"></i>
          </li>
      </ul>
  </div>
</template>

<script>
export default {
    name: 'Footer',
    data () {
        return {
            list: [
                {
                    id: 0,
                    icon: 'iconfont icon-accountfilling',
                    title: '后台管理',
                    href: '/looding'
                },{
                    id: 1,
                    icon: 'iconfont icon-msnui-list',
                    title: '文章列表',
                    href: '/list'
                },{
                    id: 2,
                    icon: 'iconfont icon-shezhi',
                    title: '设置',
                    href: '/setUp'
                
                }
                
            ],
            showId:1
        }
    },
    methods: {
        goList (href,id) {
            if (href) {
                this.$router.push({ path: href })
            } 
            //添加高亮
            this.showId = id;   
        }
    },
    created () {
        var href = this.$route.path;
        var self = this;
        $(this.list).each(function(index,item){
            if(item.href == href){
                self.showId = item.id
            }
        })
    }
}
</script>

<style lang="scss" scoped>
    #footer{
       
        ul{
            background:$background;
            border-radius: 6px;
            border:1px solid #696e71;
            li{
                cursor: pointer;
                display: flex;
                align-items: center;
                width:89px;
                height:89px;
                border-bottom:1px solid #5578a6;
                &:last-child{
                    border:0;
                }
                i{
                    margin:0 auto;
                    display: block;
                    width:50px;
                    height:50px;
                    border-radius: 50%;
                    background:#fff;
                    line-height: 50px;
                    text-align: center;
                    font-size:22px;
                    color:$background;
                }
                &:hover{
                    transition: all 1s;
                    background:#fff;
                    i{
                        background:$background;
                        color:#fff;
                    }
                    &:last-child{
                        border-radius: 0 0 6px 6px;
                    }
                    &:first-child{
                        border-radius: 6px 6px 0 0;
                    }
                }
                
            }
            .active{
        
                background:#fff;
                i{
                    background:$background;
                    color:#fff;
                }
                &:last-child{
                    border-radius: 0 0 6px 6px;
                }
                &:first-child{
                    border-radius: 6px 6px 0 0;
                }
            }
        }
    }

    
    @media screen and (max-width: 1180px) {
      #footer{
          
          ul{
              display: flex;
              justify-content: space-between;
              width:269px;
              li{
                  
                   &:hover{
                    transition: all 1s;
                    background:#fff;
                    i{
                        background:$background;
                        color:#fff;
                        }
                        &:last-child{
                            border-radius: 0  6px 6px 0;
                        }
                        &:first-child{
                            border-radius: 6px 0 0 6px;
                        }
                    }
                }
          }
        
          
         
      }
  
    }

    @media screen  and (max-width: 560px){
      #footer{
            position: fixed;
            bottom:0;
            right:0;
            left:0;
          ul{
                width:100%;
                height:vw(110);
                border-radius:0;
                display: flex;
                li{
                     border-radius: 0;
                    height:100%;
                    flex:1;
                    i{
                        width:vw(80);
                        height:vw(80);
                        font-size:vw(40);
                        line-height: vw(80);
                       
                    }
                    
                }
                .active{
                    i{

                    }
                    &:last-child{
                        border-radius:0;
                    }
                    &:first-child{
                        border-radius: 0;
                    }
                }
              
          }
         
         
      }
  
    }
</style>





