<template>
    <div id="edit">
       
        <div class="editContent" v-if="arr">
            <form>
                <label for="title">title:</label>
                <input type="text" v-model= "arr.title" id="title" name="title"/>
            </form>
            <form>
                <label for="brief">brief:</label>
                <textarea v-model= "arr.brief" id="brief" name="brief"></textarea>
            </form>
            <UE :ueditor= "arr.ueditor"></UE>
        </div>
        <span class="preservation" @click= "preservationData">保   存</span>
    </div>
</template>

<script>
import UE from "../Ueditor/Ueditor"

export default {
    name : "Edit",
    data () {
        return {
            arr:null,
            url:'http://45.76.169.191:80'
            
        }
    },
    methods:{
        
        //更新数据
        preservationData () {
            var self = this;
            var url;
            if(this.arr.id){
               
               url = 'http://45.76.169.191:80/updateDate'
            }else{
               
                url= 'http://45.76.169.191:80/insertDate'
            }
            
            this.arr.ueditor = this.$children[0].editorContent;
           
            
            $.ajax({
                type:'post',
                url:url,
                data:self.arr,
                success:function(data){
                    if(JSON.parse(data).status){
                        alert("保存成功")
                    }else{
                        alert("保存失败")
                    }
                }
            })
        }
    },
    mounted () {
        var id = this.$route.query.id;
        var self = this;
        //编辑
        if(id){
            $.ajax({
                type:'post',
                url:`${self.url}/getList`,
                data:{
                    id:id
                },
                success:function(data){
                    console.log(JSON.parse(data).data[0])
                    self.arr = JSON.parse(data).data[0];
                }
            })
        }else {
            this.arr = {
                title:"",
                brief:'',
                img:'',
                browse:0,
                Fabulous:0,
                water:0,
                ueditor:''
            }       
        }
    
    },
    components:{UE},
    
    
   
   
   
}
</script>

<style lang="scss" scoped>
    #edit{
        width:80%;
        margin:10px auto;
        .editContent{
            font-size:14px;
            margin-top:20px;
           
            form{
                border-radius: 4px;
                background:#ddd;
                display: flex;
                justify-content: space-between;
                margin:20px auto;
                &:first-child label{
                        margin-left:4px;
                }
                label{
                    text-align: center;
                    align-items:center;
                    align-content:center;
                    flex:0.05;
                    color:#130b0b;
                    line-height:30px;
                }
                span{
                    color:red;
                }
                input,textarea{
                    flex:0.95;
                    box-sizing:border-box;
                    border:0;
                    outline: none;
                    padding:5px 8px;
                    height:70px;
                    border-radius: 4px;
                    background:#ddd;
                }
                input{
                    height:30px;
                }
                
            }
        }
        .preservation{
            display: block;
            width:130px;
            height:40px;
            background:$background;
            color:#FFF;
            text-align: center;
            line-height:40px;
            margin:20px auto;
            cursor: pointer;
            border-radius: 4px;
        }
    }
</style>







